@import "@/styles/_variables.scss";@import "@/styles/_mixins.scss";@import "@/styles/_fonts.scss";@import "@/styles/_reset.scss";@import "@/styles/_global.scss";
.title-details {
  height: 100%;
  $full-height : 100;
  $title-part : 40;


  .title,
  .details {
    display: flex;
  }

  .title {
    align-items: center;
    flex-direction: column-reverse;
    justify-content: space-evenly;
    height: $title-part * 1%;
  }
  .details {
    align-items: stretch;
    justify-content: space-around;
    height: ($full-height -  $title-part) * 1%;

  }

  @include for-size(desktop-up) {
    .title {
      flex-direction: column;
    }
    .title,
    .details {
      align-items: center;
    }
    
  }

}

@include for-size(desktop-up) {
  height: 100%;
  $full-height : 100;
  $title-part : 50;
 
  .title {
    height: $title-part * 1%;
  }
  .details {
    height: ($full-height -  $title-part) * 1%;
  }
}